@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.header {
  background: var(--primary-green);
  width: 100%;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: end;
          justify-content: end;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
}

.header-logo {
  width: 30%;
  max-width: 200px;
}
.carousel {
  overflow-x: scroll;
  width: 96%;
  position: relative;
  left: 2%;
}

.carousel::-webkit-scrollbar {
  height: 8px;
}

.carousel::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}

.carousel::-webkit-scrollbar-track {
  background: #e1e1e1;
  border-radius: 4px;
}

.carousel__container {
  white-space: nowrap;
  margin: 0;
  margin-top: 100px;
  padding-bottom: 10px;
}

.price {
  font-size: 1.5rem;
  font-weight: bold;
  margin-top: 5px;
}

.price strong {
  color: var(--primary-green);
  font-size: 1.5rem;
}


.units {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 20%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1rem;
}

.units-change {
  color: var(--primary-green);
  font-weight: bold;
 
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 2rem;
  cursor: pointer;
}

.units-count {
  font-weight: bold;
  font-size: 1.5rem;
}

.weight {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
}

.weight h3 {
  color: #25c16a;
}

.weight p {
  background: #25c16a;
  width: 25%;
  color: white;
  font-weight: bold;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 25px;
}
.delete-item {
  width: 30px;
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.delete-item  img {
  width: 80%;
  height: 80%;
  object-fit: scale-down;
}
.car-details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 1px 4px rgb(0 0 0 / 25%);
  padding: 2%;
  margin-top: 2%;
  border-radius: 5px;
  width: 99%;  
}

.car-details__description {
  width: 40%;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
}

.car-details__description .product__title{
  font-size: 1.5rem;
  height: auto;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.car-details__description p {
  width: 100%;
}

.car-details__description .weight {
  margin: 0;
  width: 100%;
}

.car-details__description .weight p {
  width: 35%;
}

.car-details .price {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.5rem;
}

.car-details .price strong {
  font-size: 1.5rem;
}


.car {
  position: fixed;
  background: white;
  width: 100%;
  max-width: 600px;
  padding: 2%;
  padding-top: 20px;
  height: 100%;
  z-index: 2;
  top: 0;
  right: -100%;
  transition: 1s;
}

.car-carousel {
  width: 100%;
  overflow-y: scroll;
  height: 75%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.car--open {
  right: 0;
  transition: 1s;
}

.car-back {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 5px;
  color: var(--primary-green);
  border: solid 1px var(--primary-green);
  background: white;
  width: auto;
  height: 30px;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
}

.car-back:active {
  background: rgb(225, 255, 232);
}

.car h2{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
}

.items {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-bottom: 10px;
}

.items p {
  font-size: 1.5rem;
  font-weight: bold;
}

.car-content-image {
  position: absolute;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid var(--primary-green);
  border-radius: 5px;
  top: 90px;
  background: white;
  cursor: pointer;
  padding: 10px;
  width: 100%;
  max-width: 150px;
  right: 2%
}

.car-content-image:active {
  background: rgb(225, 255, 232);
}

.car-content-image h3 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
  width: 30px;
  height: 30px;
  color: var(--primary-green);
}

.car-content-image p {
  font-size: 2rem;
  color: var(--primary-green);
  font-weight: bold;
}

.car-content-image img {
  width: 35px;
}

@media only screen and (min-width: 768px) {
  .car h2{
    font-size: 2rem;
  }
  
}

.stamp-image-tooltip {
  margin-top: 10px;
}

.stamp-image-tooltip img {
  width: 90%;
  margin-left: -150%;
}

.stamp-image-tooltip-text {
  display: none;
  width: 90px;
  background-color: #ede8e8;
  color: #ce0e0e;
  border-radius: 5px 5px 5px 5px;
  padding: 5px;
  position: absolute;
  margin-top: -25px;
  margin-left: -113px;
  box-shadow: 0 4px 4px rgb(0 0 0 / 20%);
}

.stamp-image-tooltip:hover .stamp-image-tooltip-text {
  display: block;
}
.product {
  overflow: hidden;
  position: relative;
  display: inline-block;
  width: 70%;
  max-width: 250px;
  padding: 10px;
  padding-bottom: 0;
  -webkit-justify-content: center;
          justify-content: center;
  border: solid 1px var(--primary-green);
  border-radius: 5px;
  margin-left: 35px;
}

.product__container-img {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 180px;
  border-bottom: 2px solid #e3e3e3;
}

.product__container-img-product {
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.product__container-img-product img {
  width: 65%;
  object-fit: contain;
  max-height: 180px;
}

.product__container-img-stamp {
  width: 10%;
  margin-right: -10%;
  text-align: center;
  margin-top: 5%;
}

.product__details {
  height: 90px;
}

.product__title {
  margin-top: 10px;
  white-space: normal;
  height: 45px;
  font-size: 1.3rem;
}

.product__amount {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: left;
          justify-content: left;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.product__amount p {
  font-size: 1rem;
  color: gray;
  padding-left: 5px;
  margin-bottom: 10px;
}

.product:hover  .botton-add-car {
  display: block;
  transition: 0.5s;
  visibility: visible;
}

.botton-add-car {
  visibility: hidden;
  background: var(--primary-green);
  width: calc(100% + 20px);
  height: 40px;
  margin-left: -10px;
  margin-top: 10px;
  cursor: pointer;
}

.botton-add-car:active {
  background: green;
}

.botton-add-car p {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  height: 100%;
}

@media only screen and (min-width: 768px) {
  .product__title {
    font-size: 1.6rem;
  }
  .botton-add-car p {
    font-size: 1.5rem;
  }
  
}
:root {
  --primary-green: #25c16a;
}

* {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

html {
  
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}
